import { Container, Point, Renderer, Sprite, Texture } from "pixi.js";
import * as keycode from 'keycode-js';
import * as TWEEN from '@tweenjs/tween.js';
import { MainMenu } from './mainmenu';
import { PersistenUI } from './persistentui';
import { CreditMeter } from './creditmeter';
import { BagChecking } from "./checkingui";
import { YouWin } from './youwin';
import { BetType, GameManager, GameState } from './gamemanager';
import { AudioManager } from './audiomanager';



export class LaglagBala extends Container {
    private m_bgSprite: Sprite;
    private m_reelSprite: Sprite;

    private m_renderer: Renderer;
    private m_interactionManager : PIXI.interaction.InteractionManager;

    private persistenui: PersistenUI;
    private creditMeter: CreditMeter;
    private mainmenu: MainMenu;
    private bagChecking: BagChecking;
    private youWin: YouWin;

    private gameManager: GameManager;
    private audioManager: AudioManager;
    private m_loader: PIXI.Loader;

    constructor(pRenderer: PIXI.Renderer) {
        super();

        this.m_renderer = pRenderer;
        this.onStart();

        this.m_interactionManager = new PIXI.interaction.InteractionManager(this.m_renderer);

        this.m_interactionManager.on('mousedown', this.onButtonDown);

        document.addEventListener('keydown', this.onKeyDown);
    }

    onStart() {
        this.sortableChildren = true;

        this.audioManager = new AudioManager();
        

        this.m_bgSprite = new Sprite(Texture.from('assets/laglagbala/bg1.png'));
        this.m_bgSprite.zIndex = 0;
        this.addChild(this.m_bgSprite);

       
        
        this.mainmenu = new MainMenu()
        this.addChild(this.mainmenu);

        this.mainmenu.yesCallback = this.yesCallback;
        this.mainmenu.noCallback = this.noCallback;

        this.bagChecking = new BagChecking(this.m_renderer, this.audioManager);
        this.bagChecking.resultLoadedCallback = this.resultCallback;
        this.addChild(this.bagChecking);

        this.youWin = new YouWin(this.m_renderer, this.audioManager);
        this.youWin.youWinCoinsOutCallback = this.youWonCallback;
        this.addChild(this.youWin);

        this.persistenui = new PersistenUI();
        this.addChild(this.persistenui);

        this.creditMeter = new CreditMeter();
        this.addChild(this.creditMeter);
        this.creditMeter.addCallback = this.addCallback;
        this.creditMeter.subtrackCallback = this.subtractCallback;

        this.gameManager = new GameManager(this.creditMeter);

        this.updateState();

        this.audioManager.playBGM();
    }

    OnDestroy() {
    }

    onUpdate(pDeltaTime: number) {

        if(this.bagChecking)
        {
            this.bagChecking.update(pDeltaTime);
        }

        if(this.youWin)
        {
            this.youWin.update(pDeltaTime);
        }

        TWEEN.update();
    }

    public updateState()
    {
        switch(this.gameManager.gameState)
        {
            case GameState.Betting:{
                this.mainmenu.show()
                this.bagChecking.hide();
                this.youWin.hide();
                this.creditMeter.enableBettingButtons();
            }
            break;
            case GameState.WaitingForResult:{
                this.mainmenu.hide()
                this.bagChecking.show();
                this.youWin.hide();
                this.creditMeter.disableBettingButtons();
            }
            break;
            case GameState.Result:{
                this.mainmenu.hide()
                this.bagChecking.hide();
                this.creditMeter.disableBettingButtons();

                let isWin = false;
                if(this.gameManager.getPlayerBetType() == this.gameManager.result)
                {
                    isWin = true;

                    this.gameManager.addWinningMoney(this.gameManager.getPlayerBet() * 2);
                }
                this.youWin.show(isWin, this.gameManager.result);
            }
            break;
        }
    }

    public yesCallback : () => void = () => {
        console.log("yes bet");

        if(this.gameManager.getPlayerBet() <= 0)
        {
            alert("Bet First!")
            return;
        }

        this.audioManager.playClickYesOrNo();
        this.gameManager.setPlayerBetType(BetType.WithBala);
        this.updateState();
    }

    public noCallback : () => void = () => {
        console.log("no bet");

        if(this.gameManager.getPlayerBet() <= 0)
        {
            alert("Bet First!")
            return;
        }

        this.audioManager.playClickYesOrNo();
        this.gameManager.setPlayerBetType(BetType.WithNoBala);
        this.updateState();
    }

    public addCallback : () => void = () => {
        console.log("add bet");

        this.audioManager.playClickBet();
        this.gameManager.setPlayerBetMoney(10);
    }

    public subtractCallback : () => void = () => {
        console.log("subtract bet");

        this.audioManager.playClickBet();
        this.gameManager.setPlayerBetMoney(-10);
    }

    public resultCallback : (pRes: BetType) => void = (pRes: BetType) => {
        console.log("RESULT " + pRes);
        this.gameManager.gameState = GameState.Result;
        this.gameManager.result = pRes;

        this.updateState();

    }

    public youWonCallback : () => void = () => {
       
        this.gameManager.gameState = GameState.Betting;

         this.gameManager.resetBet();

        this.updateState();

    }

    /**
    * Mouse trigger
    */
    private onButtonDown = () => {
        console.log(this.m_interactionManager.mouse.global)
    }

    /**
    * Keyboard trigger
    */
    //#region Keyboard callback
    private onKeyDown = (e: KeyboardEvent) => {
        switch (e.keyCode) {
            case keycode.KEY_A:
                {
                    this.youWin.show(true, BetType.WithBala);
                }
                break;
            case keycode.KEY_B:
                {
                    this.youWin.show(true, BetType.WithNoBala);
                }
                break;
            case keycode.KEY_C:
                {
                    this.youWin.show(false, BetType.WithBala);
                }
                break;
            case keycode.KEY_D:
                {
                    this.youWin.show(false, BetType.WithNoBala);
                }
                break;
            case keycode.KEY_1:
            case keycode.KEY_NUMPAD1:
                {
                    this.youWin.playGlitterParticle();
                }
                break;
            case keycode.KEY_2:
            case keycode.KEY_NUMPAD2:
                {
                }
                break;
            case keycode.KEY_3:
            case keycode.KEY_NUMPAD3:
                {
                }
                break;
            case keycode.KEY_4:
            case keycode.KEY_NUMPAD4:
                {
                }
                break;
            case keycode.KEY_5:
            case keycode.KEY_NUMPAD5:
                {
                }
                break;
            case keycode.KEY_SPACE:
                {

                }
                break;
            default: break;
        }
    }
}