import { Container, Sprite, Texture, Text } from "pixi.js";


export class CreditMeter extends Container
{
    private phpIcon: Sprite
    private betIcon: Sprite;

    private addIcon: Sprite
    private minusIcon: Sprite;

    private playerMoneyTxt: Text;
    private playerBetTxt: Text;

    private disableBetting: boolean;

    public addCallback: ()=>void;
    public subtrackCallback: ()=>void;
    constructor()
    {
        super();

        this.disableBetting = false;

        this.phpIcon = new Sprite(Texture.from('assets/laglagbala/1.png'));
        this.addChild(this.phpIcon);

        this.phpIcon.scale.set(0.1);
        this.phpIcon.position.x = 50;
        this.phpIcon.position.y = 960;

        this.betIcon= new Sprite(Texture.from('assets/laglagbala/3.png'));
        this.addChild(this.betIcon);

        this.betIcon.scale.set(0.1);
        this.betIcon.position.x = 400;
        this.betIcon.position.y = 960;

        this.addIcon = new Sprite(Texture.from('assets/laglagbala/9.png'));
        this.addChild(this.addIcon);

        this.addIcon.scale.set(0.05);
        this.addIcon.position.x = 700;
        this.addIcon.position.y = 980;
        this.addIcon.interactive = true;
        this.addIcon.buttonMode = true;

        this.addIcon.on('click', () => {

            if( this.disableBetting ) return;

            console.log('add Button Pressed!');
            // Add your button's action here
            if(this.addCallback)
            {
                this.addCallback();
            }
        });

        this.minusIcon= new Sprite(Texture.from('assets/laglagbala/10.png'));
        this.addChild(this.minusIcon);

        this.minusIcon.scale.set(0.05);
        this.minusIcon.position.x = 750;
        this.minusIcon.position.y = 980;
        this.minusIcon.interactive = true;
        this.minusIcon.buttonMode = true;

        this.minusIcon.on('click', () => {

            if( this.disableBetting ) return;

            console.log('minusIcon Button Pressed!');
            // Add your button's action here
            if(this.subtrackCallback)
            {
                this.subtrackCallback();
            }
        });

        var stylePlayerMoney = new PIXI.TextStyle({
            fontFamily: 'Arial',
            fontSize: 50,
            fontStyle: 'normal',
            fontWeight: 'bold',
            fill: ['#f7ef8a', '#ae8624', '#f7ef8a'], // gradient
            stroke: '#000000',
            strokeThickness: 5,
            dropShadow: true,
            dropShadowColor: '#ffffff',
            dropShadowBlur: 1,
            dropShadowAngle: 1,
            dropShadowDistance: 1,
            wordWrap: true,
            wordWrapWidth: 440
        });

        const formatterstring = (1000.00).toLocaleString('en-US', {style: 'decimal', minimumFractionDigits: 2});
        this.playerMoneyTxt = new PIXI.Text(formatterstring, stylePlayerMoney);
        this.playerMoneyTxt.x = 150;
        this.playerMoneyTxt.y = 970;

        this.addChild(this.playerMoneyTxt);

         var stylePlayerBet = new PIXI.TextStyle({
            fontFamily: 'Arial',
            fontSize: 50,
            fontStyle: 'normal',
            fontWeight: 'bold',
            fill: ['#0052A2', '#30e7ed', '#0052A2'], // gradient
            stroke: '#000000',
            strokeThickness: 5,
            dropShadow: true,
            dropShadowColor: '#ffffff',
            dropShadowBlur: 1,
            dropShadowAngle: 1,
            dropShadowDistance: 1,
            wordWrap: true,
            wordWrapWidth: 440
        });

        const betformatterstring = (100.00).toLocaleString('en-US', {style: 'decimal', minimumFractionDigits: 2});

        this.playerBetTxt = new PIXI.Text(betformatterstring, stylePlayerBet);
        this.playerBetTxt.x = 500;
        this.playerBetTxt.y = 970;

        this.addChild(this.playerBetTxt);
        
    }

    public disableBettingButtons()
    {
        this.disableBetting = true;
    }

    public enableBettingButtons()
    {
        this.disableBetting = false;
    }

    public updatePlayerMoney(pMoney: number)
    {
        this.playerMoneyTxt.text = pMoney.toString();
    }

    public updateBet(pPlayerMoney: number, pBet: number)
    {
        this.playerMoneyTxt.text = pPlayerMoney.toString();
        this.playerBetTxt.text = pBet.toString();
    }
}