import { CreditMeter } from "./creditmeter";

export enum BetType
{
    WithBala = 0,
    WithNoBala,
    Neutral
}

export enum GameState
{
    Betting = 0,
    WaitingForResult,
    Result
}

export class GameManager
{
    public gameState: GameState;
    public result: BetType;

    private playerBetType: BetType;
    private playerBetMoney: number;
    private playerMoney: number;
    private creditMeter: CreditMeter;

    constructor(pCreditMeter: CreditMeter)
    {
        this.creditMeter = pCreditMeter;
        this.gameState = GameState.Betting;

        this.playerBetType = BetType.Neutral;
        this.playerBetMoney = 0;
        this.playerMoney = 1000;

        this.creditMeter.updateBet(this.playerMoney, this.playerBetMoney);
    }

    public resetBet()
    {
         this.playerBetMoney = 0;
         this.creditMeter.updateBet(this.playerMoney, this.playerBetMoney);
    }

    public getPlayerBet()
    {
        return this.playerBetMoney;
    }

    public addWinningMoney(pMoney: number)
    {
        this.playerMoney += pMoney;
    }

    public setPlayerBetType(pBetType: BetType)
    {
        this.playerBetType = pBetType;
        this.gameState = GameState.WaitingForResult;
    }

    public getPlayerBetType()
    {
        return this.playerBetType;
    }

    public setPlayerBetMoney(pBetMoney: number)
    {
        if(this.playerMoney >= pBetMoney)
        {
           
            let prevBet = this.playerBetMoney;

            this.playerBetMoney += pBetMoney;

            if(this.playerBetMoney <= 0)
                this.playerBetMoney = 0;

            if(pBetMoney > 0)
                this.playerMoney -= pBetMoney;
            else if(pBetMoney < 0 && prevBet != 0)
                this.playerMoney -= pBetMoney;

           
             

            this.creditMeter.updateBet(this.playerMoney, this.playerBetMoney);

            console.log("Player Bet: " + this.playerBetMoney);
        }
        
    }
}