import { Container, Renderer, Sprite, Texture } from "pixi.js";
import { BetType } from "./gamemanager";
import { AudioManager } from "./audiomanager";
import * as TWEEN from '@tweenjs/tween.js';

export class BagChecking extends Container
{
    private checking: Sprite;
    private characters: Sprite;

    private m_renderer: Renderer;

    private waitingForResult: boolean;
    private delay: number = 3;
    private delayTimer: number = 0;

    public resultLoadedCallback: (pRes: BetType)=>void;

    private withBalaChance = 0.5;

    private audioManager: AudioManager;

    private tweenFadeIn: any;
    private targetAlpha: any = {};

    constructor(pRenderer: Renderer, pAudioManager: AudioManager)
    {
        super();

        this.audioManager = pAudioManager;

        this.waitingForResult = false;
        this.m_renderer = pRenderer;

        this.characters = new Sprite(Texture.from('assets/laglagbala/CheckingCharacters.png'));
        this.characters.anchor.set(0.5);
         this.characters.scale.set(2);
        this.characters.position.x = this.m_renderer.width * 0.5;
        this.characters.position.y = (this.m_renderer.height * 0.5) ;
        this.addChild(this.characters);

        this.checking = new Sprite(Texture.from('assets/laglagbala/BagChecked.png'));
        this.checking.anchor.set(0.5);
        this.checking.position.x = this.m_renderer.width * 0.5;
        this.checking.position.y = (this.m_renderer.height * 0.5) + 300;
        this.addChild(this.checking);
    

        
    }

    public show()
    {
        //this.alpha = 1;

        this.delayTimer = 0;
        this.waitingForResult = true;

        this.audioManager.playLuggageScane();

        this.fadeIn();
    }

     private fadeIn()
        {
            if(this.tweenFadeIn)
            {
                this.tweenFadeIn = null;
            } 
    
            this.targetAlpha = {a: 0};
           
    
            this.tweenFadeIn = new TWEEN.Tween(this.targetAlpha)
            .to({a: 1}, 1000)
            .easing(TWEEN.Easing.Linear.None)
            .onUpdate((ob, elapsed) => {
    
                this.alpha = this.targetAlpha.a;
    
            }).onStart(() =>{
    
            })
            .onComplete(() => 
            {
                this.alpha = 1;
            })
            .start();
        }

    public hide()
    {
        this.audioManager.stopLuggageScane();
        this.alpha = 0;
    }

    public update(pDeltaTime: number)
    {

        if(this.waitingForResult)
        {
            this.delayTimer += pDeltaTime;

            if(this.delayTimer >= this.delay)
            {
                this.waitingForResult = false;

                let res = this.generateRandomResult();
                if(this.resultLoadedCallback)
                    this.resultLoadedCallback(res);
            }
        }
    }

    private generateRandomResult()
    {
        let random = Math.random();

        if(random < this.withBalaChance)
        {
            return BetType.WithBala;
        }

        return BetType.WithNoBala;
    }
}