

import * as PIXI from 'pixi.js';


window.PIXI = PIXI;

import { LaglagBala } from './game/laglagbala/laglagbala';

const ticker = new PIXI.Ticker();

export const LANDSCAPE: boolean = true;

const default_width = 1920;
const default_height = 1080;

const width: number = LANDSCAPE ? default_width : default_height;
const height: number = LANDSCAPE ? default_height : default_width;

export class Main {

    private renderer: PIXI.Renderer;
    private ratio: number;

    private world: PIXI.Container;

    private laglagbala: LaglagBala;

    constructor() {
        this.ratio = width / height;
        this.renderer = new PIXI.Renderer({ width: width, height: height });
        this.renderer.autoDensity = true;

        this.onUpdate = this.onUpdate.bind(this);
        this.onResize = this.onResize.bind(this);

        window.onresize = this.onResize;

        this.world = new PIXI.Container();

        this.onCreate();
        
    }

    onCreate(): void {
        const gameContainer = document.createElement('div');
        gameContainer.id = "gameContainer";
        document.body.appendChild(gameContainer);
        gameContainer.appendChild(this.renderer.view);

        this.laglagbala = new LaglagBala(this.renderer);
        this.world.addChild(this.laglagbala);

        this.onInit();
    }

    onInit(): void {
        this.onResize();
        this.onUpdate();
    }

    onResize(): void {
        if (window.innerWidth / window.innerHeight >= this.ratio) {
            var w = window.innerHeight * this.ratio;
            var h = window.innerHeight;
        } else {
            var w = window.innerWidth;
            var h = window.innerWidth / this.ratio;

        }
        this.renderer.view.style.width = w + 'px';
        this.renderer.view.style.height = h + 'px';

        this.renderScene();
    }

    onUpdate(): void {
        const deltatime = ticker.deltaMS * 0.001;

        this.updateScene(deltatime);
        this.renderScene();

        requestAnimationFrame(this.onUpdate);

        ticker.update(performance.now());
    }

    private updateScene(pDeltatime: number): void {

        if (this.laglagbala) {
            this.laglagbala.onUpdate(pDeltatime);
        }

    }

    private renderScene(): void {
        this.renderer.render(this.world);
    }

}