import { Container, Sprite, Texture } from "pixi.js";
import { BagChecking } from './checkingui';
import { GameManager } from "./gamemanager";


export class MainMenu extends Container
{
    private title: Sprite;
    private characters: Sprite;

    private yesBtn: Sprite
    private noBtn: Sprite
    private infoBtn: Sprite

    private gameManager: GameManager;

    public yesCallback: ()=>void;
    public noCallback: ()=>void;

    constructor()
    {
        super(); 

        this.title = new Sprite(Texture.from('assets/laglagbala/11.png'));
        this.title.position.y = -100;
        this.addChild(this.title);

        this.characters = new Sprite(Texture.from('assets/laglagbala/HomeCharacters.png'));
        this.characters.position.x = 900;
        this.characters.position.y = 100;
        this.addChild(this.characters);

        this.yesBtn = new Sprite(Texture.from('assets/laglagbala/7.png'));
        this.addChild(this.yesBtn);

        this.yesBtn.interactive = true;
        this.yesBtn.buttonMode = true;
        this.yesBtn.scale.set(0.3);
        this.yesBtn.position.x = 150;
        this.yesBtn.position.y = 600;

        this.yesBtn.on('click', () => {
            console.log('yesBtn Button Pressed!');
            // Add your button's action here
            if(this.yesCallback)
            {
                this.yesCallback();
            }
        });

        this.noBtn = new Sprite(Texture.from('assets/laglagbala/8.png'));
        this.addChild(this.noBtn);

        this.noBtn.interactive = true;
        this.noBtn.buttonMode = true;
        this.noBtn.scale.set(0.3);
        this.noBtn.position.x = 400;
        this.noBtn.position.y = 600;

        this.noBtn.on('click', () => {
            console.log('noBtn Button Pressed!');
            // Add your button's action here
             if(this.noCallback)
            {
                this.noCallback();
            }
        });
        
    }

    public show()
    {
        this.alpha = 1;
    }

    public hide()
    {
        this.alpha = 0;
    }
}