import { Container, Sprite, Texture } from "pixi.js";


export class PersistenUI extends Container
{
    private infoBtn: Sprite
    private akalako88logo: Sprite;

    constructor()
    {
        super();


        this.infoBtn = new Sprite(Texture.from('assets/laglagbala/5.png'));
        this.addChild(this.infoBtn);

        this.infoBtn.interactive = true;
        this.infoBtn.buttonMode = true;
        this.infoBtn.scale.set(0.15);
        this.infoBtn.position.x = 1750;
        this.infoBtn.position.y = 50;

        this.infoBtn.on('click', () => {
            console.log('infoBtn Button Pressed!');
            // Add your button's action here
        });

        this.akalako88logo= new Sprite(Texture.from('assets/laglagbala/12.png'));
        this.addChild(this.akalako88logo);

        this.akalako88logo.scale.set(0.3);
        this.akalako88logo.position.x = 1650;
        this.akalako88logo.position.y = 900;
        
    }
}