import "pixi-sound"; 

import { default as pixiSound } from 'pixi-sound';

export class AudioManager
{
    constructor()
    {
        pixiSound.add('bgm', 'assets/laglagbala/audio/BGmusic.mp3');
        pixiSound.add('bet', 'assets/laglagbala/audio/ClickBetButton.mp3');
        pixiSound.add('yesorno', 'assets/laglagbala/audio/ClickYesorNo.mp3');
        pixiSound.add('luggagescan', 'assets/laglagbala/audio/LuggageScan.mp3');
        pixiSound.add('win', 'assets/laglagbala/audio/Win.mp3');
        pixiSound.add('lose', 'assets/laglagbala/audio/Lose.mp3');
    }

    playBGM()
    {
       
         pixiSound.play('bgm', {loop: true});
    }

    stopBGM()
    {
       
         pixiSound.stop('bgm');
    }

    playLuggageScane()
    {
       
         pixiSound.play('luggagescan', {loop: true});
    }

    stopLuggageScane()
    {
       
         pixiSound.stop('luggagescan');
    }

    playWin()
    {
       
         pixiSound.play('win', {loop: true});
    }

    stopWin()
    {
       
         pixiSound.stop('win');
    }

    playLose()
    {
       
         pixiSound.play('lose', {loop: true});
    }

    stopLose()
    {
       
         pixiSound.stop('lose');
    }

    playClickBet()
    {
       
         pixiSound.play('bet');
    }

    playClickYesOrNo()
    {
       
         pixiSound.play('yesorno');
    }

}