import { Container, Renderer, Sprite, Texture } from "pixi.js";
import { BetType } from "./gamemanager";
import { Emitter } from "pixi-particles";
import { AudioManager } from "./audiomanager";
import * as TWEEN from '@tweenjs/tween.js';

export class YouWin extends Container
{
    private characters: Sprite;
    private winLoseLabel: Sprite;

    private m_renderer: Renderer;

    private winWithBalaTex: Texture;
    private winNoBalaTex: Texture;

    private loseWithBalaTex: Texture;
    private loseNoBalaTex: Texture;

    private winLabel: Texture;
    private loseLabel: Texture;

    private glitterParticleContainer: Container;
    private glitterParticles : Emitter;

    private bulletParticleContainer: Container;
    private bulletParticles : Emitter;

    private coinsOutEnable: boolean = false;
    private delay: number = 5;
    private delayTimer: number = 0;

    private enable: boolean = false;
    private audioManager: AudioManager;

    private tweenFadeIn: any;
    private targetAlpha: any = {};

    public youWinCoinsOutCallback: ()=>void;

    constructor(pRenderer: Renderer, pAudioManager: AudioManager)
    {
        super();

        this.audioManager = pAudioManager;
        this.m_renderer = pRenderer;

        
        //this.addChild(this.characters);
    

        this.winWithBalaTex = Texture.from('assets/laglagbala/WinwithBala.png');
        this.winNoBalaTex = Texture.from('assets/laglagbala/WinNoBala.png');

        this.loseWithBalaTex = Texture.from('assets/laglagbala/LosewithBala.png');
        this.loseNoBalaTex = Texture.from('assets/laglagbala/LoseNoBala.png');

        this.winLabel = Texture.from('assets/laglagbala/OverlayWin.png');
        this.loseLabel = Texture.from('assets/laglagbala/OverlayLose.png');
        

         this.createGlitterParticles();
         this.createBulletParticles();

        this.characters = new Sprite(this.loseWithBalaTex);
        this.characters.anchor.set(0.5);
        this.characters.scale.set(1);
        this.characters.position.x = this.m_renderer.width * 0.5;
        this.characters.position.y = (this.m_renderer.height * 0.5);
        this.addChild(this.characters);

        this.characters.alpha = 0;

        this.winLoseLabel = new Sprite(this.loseWithBalaTex);
        this.winLoseLabel.anchor.set(0.5);
        this.winLoseLabel.scale.set(1);
        this.winLoseLabel.position.x = this.m_renderer.width * 0.5;
        this.winLoseLabel.position.y = (this.m_renderer.height * 0.5) + 200 ;
        this.addChild(this.winLoseLabel);

        this.winLoseLabel.alpha = 0;

        
    }

    public update(pDeltaTime: number)
    {
        if(this.coinsOutEnable)
        {
            this.delayTimer += pDeltaTime;

            if(this.delayTimer >= this.delay)
            {
                this.enable = false;
                this.coinsOutEnable = false;

               this.glitterParticles.emit = false;
               this.bulletParticles.emit = false;

               this.audioManager.stopLose();
               this.audioManager.stopWin();

                if(this.youWinCoinsOutCallback)
                    this.youWinCoinsOutCallback();
            }
        }
        
        if(this.glitterParticles)
        {
            this.glitterParticles.update(pDeltaTime);
        }

         if(this.bulletParticles)
        {
            this.bulletParticles.update(pDeltaTime);
        }
    }

    public show(pIsWin: boolean, pBetType: BetType)
    {
        if(this.enable)
            return;

        this.enable = true;

        if(pIsWin && pBetType == BetType.WithBala)
        {
            this.winWithBala();

            this.glitterParticles.playOnce();

            this.audioManager.playWin();
        }
        else if(pIsWin && pBetType == BetType.WithNoBala)
        {
            this.winNoBala();
            this.glitterParticles.playOnce();

            this.audioManager.playWin();
        }
        else if(!pIsWin && pBetType == BetType.WithBala)
        {
            this.loseWithBala();
            this.bulletParticles.emit = true;

            this.audioManager.playLose();
        }
        else if(!pIsWin && pBetType == BetType.WithNoBala)
        {
            this.loseNoBala();
            this.bulletParticles.emit = true;

            this.audioManager.playLose();
        }

        this.coinsOutEnable = true;
        this.delayTimer = 0;
    }

    public hide()
    {
        this.characters.alpha = 0;
        this.winLoseLabel.alpha = 0;
    }


    private winWithBala()
    {
        this.characters.texture =  this.winWithBalaTex;
        this.winLoseLabel.texture =  this.winLabel;

        //this.characters.alpha = 1;
        //this.winLoseLabel.alpha = 1;

        this.fadeIn( this.characters, this.winLoseLabel);
    }

    private winNoBala()
    {
        this.characters.texture =  this.winNoBalaTex;
        this.winLoseLabel.texture =  this.winLabel;

        //this.characters.alpha = 1;
        //this.winLoseLabel.alpha = 1;

        this.fadeIn( this.characters, this.winLoseLabel);
    }

    private loseWithBala()
    {
        this.characters.texture =  this.loseWithBalaTex;
        this.winLoseLabel.texture =  this.loseLabel;

        //this.characters.alpha = 1;
        //this.winLoseLabel.alpha = 1;

        this.fadeIn( this.characters, this.winLoseLabel);
    }

    private loseNoBala()
    {
        this.characters.texture =  this.loseNoBalaTex;
        this.winLoseLabel.texture =  this.loseLabel;

        //this.characters.alpha = 1;
        //this.winLoseLabel.alpha = 1;

        this.fadeIn( this.characters, this.winLoseLabel);
    }

    private fadeIn(pChar: Sprite, pLabel: Sprite)
    {
        if(this.tweenFadeIn)
        {
            this.tweenFadeIn = null;
        } 

        this.targetAlpha = {a: 0};
       

        this.tweenFadeIn = new TWEEN.Tween(this.targetAlpha)
        .to({a: 1}, 1000)
        .easing(TWEEN.Easing.Linear.None)
        .onUpdate((ob, elapsed) => {

            pChar.alpha = this.targetAlpha.a;
            pLabel.alpha = this.targetAlpha.a;

        }).onStart(() =>{

        })
        .onComplete(() => 
        {
            pChar.alpha = 1;
            pLabel.alpha = 1;
        })
        .start();
    }

    public playGlitterParticle()
    {
        this.glitterParticles.emit = true;
    }

    private createGlitterParticles()
    {
        this.glitterParticleContainer = new PIXI.Container();
        this.glitterParticleContainer.zIndex = 0;
        this.glitterParticleContainer.position.set(this.m_renderer.width * 0.5, this.m_renderer.height * 0.5);
        this.addChild(this.glitterParticleContainer);

        const GlittersTexture = [
            Texture.from('assets/laglagbala/1.png'),
        ]

        this.glitterParticles = new Emitter(this.glitterParticleContainer, GlittersTexture, 
            {
                alpha: {
                start: 3,
                end: 0
                },
                scale: {
                start: 0.1, //0.5
                end: 0.2, //1.5
                minimumScaleMultiplier: 0.5
                },
                color: {
                start: '#ffffff',
                end: '#ffffff'
                },
                speed: {
                start: 800,
                end: 0,
                minimumSpeedMultiplier: 1
                },
                acceleration: {
                x: 0,
                y: 0
                },
                maxSpeed: 0,
                startRotation: {
                min: 0,
                max: 360
                },
                noRotation: false,
                rotationSpeed: {
                min: 0,
                max: 360
                },
                lifetime: {
                min: 1,
                max: 4
                },
                blendMode: 'normal',
                frequency: 0.007,
                emitterLifetime: -1,
                maxParticles: 900,
                pos: {
                x: 0,
                y: 0
                },
                addAtBack: false,
                spawnType: 'burst',
                particlesPerWave: 1,
                particleSpacing: 0,
                angleStart: 0
            });

            this.glitterParticles.emit = false;
    }

    private createBulletParticles()
    {
        this.bulletParticleContainer = new PIXI.Container();
        this.bulletParticleContainer.zIndex = 0;
        this.bulletParticleContainer.position.set(0, -200);
        this.addChild(this.bulletParticleContainer);

        const BulletTex = [
            Texture.from('assets/laglagbala/bala.png'),
        ]

        this.bulletParticles = new Emitter(this.bulletParticleContainer, BulletTex, 
            {
                alpha: {
                start: 1,
                end: 1
                },
                scale: {
                start: 0.1,
                end: 0.15,
                minimumScaleMultiplier: 0.5
                },
                color: {
                start: '#ffffff',
                end: '#ffffff'
                },
                speed: {
                start: 100,
                end: 300,
                minimumSpeedMultiplier: 0.5
                },
                acceleration: {
                x: 0,
                y: 100
                },
                maxSpeed: 0,
                startRotation: {
                min: 90,
                max: 90
                },
                noRotation: false,
                rotationSpeed: {
                min: 500,
                max: -500
                },
                lifetime: {
                min: 5,
                max: 7
                },
                blendMode: 'normal',
                frequency: 0.05,
                emitterLifetime: 3,
                maxParticles: 500,
                pos: {
                x: 0,
                y: 0
                },
                spawnRect:{
                    x:0,
                    y:0,
                    w:this.m_renderer.width,
                    h:200
                },
                addAtBack: false,
                spawnType: 'rect',
                particlesPerWave: 1,
                particleSpacing: 0,
                angleStart: 0
            });

            this.bulletParticles.emit = false;
    }

}